<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.show" >
      <el-form :model="form">
        <el-form-item label="标题：" :label-width="formLabelWidth">
          <el-input v-model="form.title" autocomplete="off"></el-input>
        </el-form-item>
        <div>
          <p>图片：</p>
          <el-upload
            class="avatar-uploader"
            action="#"
            :show-file-list="false"
            :on-change="changeImg"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </div>

        <el-form-item label="菜单状态：" :label-width="formLabelWidth">
          <el-switch
            v-model="form.status"
            active-color="#409EFF"
            inactive-color="grey"
            :active-value="1"
            :inactive-value="2"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="cancel">取 消</el-button>
          <el-button type="primary" @click="confirm" v-if="info.isAdd">确 定</el-button>
          <el-button type="primary" @click="update" v-else>修 改</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { warningAlert, successAlert } from "../../../utils/alert";
import { addBanner, getOneBanner, updateBanner } from "../../../utils/request";
export default {
  props: ["info"],
  data() {
    return {
      imageUrl: "", //要显示在页面中的图片地址
      formLabelWidth: "120px",
      form: {
        title:'',
        img: '',
        status: 1,
      },
    };
  },
  methods: {
    // 对话框打开结束之后的回调
    ...mapActions({
      requestBannerList: "banner/bannerListActions",
    }),
    //上传图片
    changeImg(e) {
      // 1.处理文件大小
      if (e.size > 2 * 1024 * 1024) {
        warningAlert("文件大小不能超过2M");
        return;
      }
      // 2.处理文件后缀
      var ext = [".jpg", ".png", ".jpeg", ".gif"];
      var extName = e.name.slice(e.name.lastIndexOf("."));
      if (!ext.some((item) => item == extName)) {
        warningAlert("上传文件格式不正确");
        return;
      }
      // 上传显示的文件
      // 将文件生成url地址,并显示在页面中
      this.imageUrl = URL.createObjectURL(e.raw);
      // 将文件存放到form.img中
      this.form.img = e.raw;
    },
    cancel() {
      this.info.show = false;
      this.form = {
        title:'',
        img: '',
        status: 1,
      };
      this.imageUrl = "";
    },
    confirm() {
      // console.log(this.form);
      addBanner(this.form).then((res) => {
        successAlert(res.data.msg);
        this.cancel();
        this.requestBannerList();
      });
    },
    update() {
      updateBanner(this.form).then((res) => {
        successAlert(res.data.msg);
        this.cancel();
        this.requestBannerList();
      });
    },
    getDetail(id) {
      getOneBanner({ id }).then((res) => {
        this.form = res.data.list;
        this.form.id = id;
        this.imageUrl = this.$preImg + this.form.img;
      });
    },
  },
  computed: {
    ...mapGetters({
      bannerList: "banner/bannerList",
    }),
  },
  mounted() {
    this.requestBannerList();
  },
};
</script>

<style scoped>
p {
  width: 120px;
  height: 40px;
  text-align: right;
  vertical-align: middle;
  float: left;
  font-size: 14px;
  color: #606266;
  line-height: 40px;
  padding: 0 12px 0 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.avatar-uploader >>> .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader >>> .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>


